let requestCounter = 0;
export const doRequest = async function (method, endpoint, credentials, payload) {
  const { merchantId, userId, secret } = credentials
  console.debug(`[${++requestCounter}] REQUEST`, method, endpoint, credentials, payload)
  let response;
  try {
    response = await axios({
      method,
      url: endpoint,
      data: payload,
      headers: {
        'Accept': 'application/vnd.mcash.api.merchant.v1+json',
        'Content-Type': 'application/json',
        'x-auka-merchant': merchantId,
        'X-Auka-User': userId,
        'Authorization': `SECRET ${secret}`
      },
    })
    console.debug(`[${requestCounter}] RESPONSE ${response.status}:`, response.data)
    return response;
  } catch (error) {
    if (error.response) {
      console.debug(`[${requestCounter}] RESPONSE ${error.response.status}:`, error.response.data)
    } else {
      console.debug(`[${requestCounter}] RESPONSE ${error}:`, error)
    }
    console.error(error)
    throw error;
  }

};

export const getLongDynamicLink = function (shortlinkUrl, config, internal) {
  let schema = 'https';
  if (internal) schema = config.ius;
  return [
    `${schema}://${config.baseUrl}`,
    `?apn=${config.apn}`,
    `&ibi=${config.ibi}`,
    `&isi=${config.isi}`,
    `&ius=${config.ius}`,
    `&link=${encodeURIComponent(`https://${config.env}://qr/${shortlinkUrl}`)}`,
  ].join('');
}

export default { outcomeDescriptions, paymentStatus, paymentStatusCodes, doRequest, getLongDynamicLink };